
import { AjaxHttp } from '../../../js/ajaxHttp'
import ListCell from '../../crm/components/ListCell.vue'
import { getYMDFormateTime, getYMFormateTime, getTimestamp, isArrayType, arrContains, getYMDFormateTimestamp, getHMFormateTime, isNullValue } from "../../../js/utility.js"
import { mapState } from 'vuex'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'


export default {
name: 'checkmonth',
components: {
ListCell,
InlineCalendar
},
computed: {
...mapState([
'userInfo'
])
},
data() {
return {
show: true,
todayValue: '',
range: true,
showLastMonth: false,
showNextMonth: false,
highlightWeekend: false,
return6Rows: true,
hideHeader: false,
hideWeekList: false,
replaceTextList: { 'TODAY': '今' },
replace: false,
changeWeeksList: true,
weeksList: ['日', '一', '二', '三', '四', '五', '六 '],
useCustomFn: true,
buildSlotFn: () => '<div style="height:19px;"></div>',
disablePast: false,
disableFuture: false,

normalArray: [], //正常数组
abnormalArray: [], //异常数组

daka_status: '', //1 签到 2签退
shangban_time: '',
shangban_info: {}, //上班信息
shangban_status: '', // 展示类型
xiaban_time: '',
xiaban_info: {}, //下班信息
xiaban_status: '', // 展示类型
datalist: [],
work_time: '',
kaoqin_count: ''
}
},
mounted() {
this.getIndex(getTimestamp(new Date()));
if (this.$route.params.time === 'default') {
this.todayValue = getYMDFormateTime(getTimestamp(new Date()));
} else {
this.todayValue = this.$route.params.time;
}
this.getdetail(this.todayValue);

},
methods: {
getIndex(time) {

AjaxHttp('POST', 'm=kaoqin&a=index', { time: time }, this, function(res, wself, success) {
if (success) {
wself.normalArray = [];
wself.abnormalArray = [];
if (isArrayType(res.list)) {
for (var i = 0; i < res.list.length; i++) {
var item = res.list[i];
if (item.kaoqin_type === 1) {
wself.normalArray.push(item.date);
} else if (item.kaoqin_type === 2) {
wself.abnormalArray.push(item.date);
}
}
}

wself.markDay();
}
});
},
onChange(val) {
if (arrContains(this.normalArray, val) || arrContains(this.abnormalArray, val)) {
this.getdetail(val);
} else {
this.shangban_status = '';
this.xiaban_status = '';
}

},
getdetail(time) {
this.daka_status = ''; 
AjaxHttp('POST', 'm=kaoqin&a=view', { date: time }, this, function(res, wself, success) {
if (success) {
if (res.list.length > 0) {
for (var i = 0; i < res.list.length; i++) {
var item = res.list[i];
item.daka_time = getHMFormateTime(item.daka_time);
item.status_color = wself.getStatusColor(item.status);
item.status_name = wself.getStatusName(item.status);
if (item.status === '1' || item.status === '2') {
wself.shangban_info = item;
} else if (item.status === '3' || item.status === '4') {
wself.xiaban_info = item;
}
}
}

wself.datalist = res.list;
wself.daka_status = res.daka_status; 
wself.shangban_time = res.shangban_time;
wself.xiaban_time = res.xiaban_time;
wself.work_time = res.work_time;
wself.kaoqin_count = res.kaoqin_count;
wself.shangban_status = wself.getShangban_status();
wself.xiaban_status = wself.getXiaban_status();
}
});

},
getStatusColor(status) {
if (status === '1') {
return '#198AFA'; 
} else if (status === '2') {
return '#FF8125'; 
} else if (status === '3') {
return '#FF9C00'; 
} else if (status === '4') {
return '#198AFA'; 
}
return '#198AFA';
},
getStatusName(status) {
if (status === '1') {
return '正常'; 
} else if (status === '2') {
return '迟到'; 
} else if (status === '3') {
return '早退'; 
} else if (status === '4') {
return '正常'; 
}
return '';
},
getShangban_status() {


if (this.daka_status !== '') { //'begin' 'normal' 'none'
if (this.daka_status === 0 || this.daka_status === 2 || this.daka_status === 3) { //不能打卡
var hasInfo = false; 
for (var i = 0; i < this.datalist.length; i++) {
var item = this.datalist[i];
if (item.status === '1' || item.status === '2') {
hasInfo = true;
}
}
if (hasInfo) {
return 'normal';
} else {
return 'none';
}
} else if (this.daka_status === 1) {
return 'begin';
}
}
return ''; 
},
getXiaban_status() {

if (this.daka_status !== '') { //'begin' 'normal' 'none'
if (this.daka_status === 0) { //不能打卡
var hasInfo = false; 
for (var i = 0; i < this.datalist.length; i++) {
var item = this.datalist[i];
if (item.status === '3' || item.status === '4') {
hasInfo = true;
}
}
if (hasInfo) {
return 'normal';
} else {
return 'none';
}
} else if (this.daka_status === 2) {
return 'begin';
} else if (this.daka_status === 3) { //可更新打卡
return 'normal';
}
}
return ''; 
},
onViewChange(val, count) {

if (count !== 0) {
var selectTime = val.year + '-' + val.month;
this.todayValue = val.year + '-01-01';
this.getdetail(this.todayValue);
this.getIndex(this.getYMFormateTimestamp(selectTime));
}
},
getYMFormateTimestamp(time) {
time = time + '-01 00:00:00';
var t = time.split(/[- :]/);
var d = new Date(t[0], t[1] - 1, t[2], t[3], t[4], t[5]);
var timestamp = new Date(d).getTime().toString();
timestamp = timestamp.substr(0, 10);
return timestamp;
},
markDay() {
var self = this;
this.buildSlotFn = (line, index, data) => {
if (arrContains(self.normalArray, data.formatedDate)) {
return '<div style="font-size:12px;text-align:center;"><span style="display:inline-block;width:5px;height:5px;background-color:#2385CE;border-radius:50%;"></span></div>';
} else if (arrContains(self.abnormalArray, data.formatedDate)) {
return '<div style="font-size:12px;text-align:center;"><span style="display:inline-block;width:5px;height:5px;background-color:#FB9043;border-radius:50%;"></span></div>';
}

return '<div style="height:19px;"></div>';
};
},
enterUser(role_id) { //进入个人详情
this.$router.push({
name: 'user',
params: {
id: role_id
}
})
},
}
}




